footer {
  text-align: right;
  position:'';  
  bottom: 0rem;
  height: 2.5rem;
  width: 100%;
  margin-top: 10%;
  margin-bottom: 10px;
}

p {
  color: #119DA4;
}

.button {
  height: 40px;
  width: 200px;
  background-color: white;
  color: black;
  border: 2px solid #119DA4;
  border-radius: 25px;
  margin: 10px;
  font-size: large;
  font-weight:normal;
   
}

.button:hover {
  filter: drop-shadow(5px 5px 5px #c4c4c4);
}


h1 {
  color: #0C7489;
  text-align: left;
  display: flex;
  align-self: center;
}

.card-body {
  background-color: #e8f8ee;
}

.art-card-container {
  display: flex;
  position: relative;
  align-items: baseline;
  justify-content: center;
  text-align: left;
}

.container {
  
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  text-align: left;
}

.icon {
  width: 50px;
  margin: 5px;

 
} 



.contact {
  height: 50%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  

}

#aboutmedescr {
  margin-top: 20px;
}

.btn-primary {
  background-color: #119DA4 !important;
  border-color: #119DA4 !important;
}

.nav-link {
color: #119DA4 !important;
}



h6 {
  color: rgb(151, 151, 151);
  
}

.art-card {
  display: flex;
  flex-direction: column;
  align-items: left;
  text-align: left;

}
@media (max-width: 991px){
  .contact{
      flex-direction: column;
  }
  .container{
      display: block;
      margin-left: auto;
      margin-right: auto;
      text-align: center;
  }
  img{
    align-content: center;
    max-width: 100% !important;
  }

  
}
